*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: var(--FONT-INTER);
}

body {
  background-color: var(--BG-COLOR);
  color: var(--TEXT-COLOR);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 62em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --LIMEGREEN: hsl(163, 72%, 41%);
  --BRIGHTRED: hsl(356, 69%, 56%);
  --FACEBOOK: hsl(208, 92%, 53%);
  --TWITTER: hsl(203, 89%, 53%);
  --INSTAGRAM-START: hsl(37, 97%, 70%);
  --INSTAGRAM-END: hsl(329, 70%, 58%);
  --YOUTUBE: hsl(348, 97%, 39%);
  --TOGGLE-BG-START: hsl(210, 78%, 56%);
  --TOGGLE-BG-END: hsl(146, 68%, 55%);
  --TOGGLE: hsl(230, 22%, 74%);
  --DARK-BG: hsl(230, 17%, 14%);
  --DARK-TOP: hsl(232, 19%, 15%);
  --DARK-CARD: hsl(228, 28%, 20%);
  --DARK-CARD-HOVER: hsl(228, 25%, 27%);
  --DARK-TEXT-GREY: hsl(228, 34%, 66%);
  --DARK-TEXT-WHITE: hsl(0, 0%, 100%);
  --LIGHT-BG: hsl(0, 0%, 100%);
  --LIGT-TOP: hsl(225, 100%, 98%);
  --LIGHT-CARD: hsl(227, 47%, 96%);
  --LIGHT-CARD-HOVER: hsl(0, 0%, 90%);
  --LIGHT-TEXT-GREY: hsl(228, 12%, 44%);
  --LIGHT-TEXT-BLUE: hsl(230, 17%, 14%);
  --BG-COLOR: var(--LIGHT-BG);
  --TEXT-COLOR: var(--LIGHT-TEXT-BLUE);
  --CARD-COLOR: var(--LIGHT-CARD);
  --TEXT-GREY: var(--LIGHT-TEXT-GREY);
  --HOVER-COLOR: var(--LIGHT-CARD-HOVER);
  --TOGGLE-COLOR: var(--LIGHT-BG);
  --TOGGLE-BG-COLOR: var(--TOGGLE);
}

@media (prefers-color-scheme: dark) {
  :root {
    --BG-COLOR: var(--DARK-BG);
    --TEXT-COLOR: var(--DARK-TEXT-WHITE);
    --CARD-COLOR: var(--DARK-CARD);
    --TEXT-GREY: var(--DARK-TEXT-GREY);
    --HOVER-COLOR: var(--DARK-CARD-HOVER);
    --TOGGLE-BG-COLOR: linear-gradient(
      225deg,
      var(--TOGGLE-BG-START) 0%,
      var(--TOGGLE-BG-END) 90%
    );
    --TOGGLE-COLOR: var(--DARK-BG);
  }
}
body.light {
  --BG-COLOR: var(--LIGHT-BG);
  --TEXT-COLOR: var(--LIGHT-TEXT-BLUE);
  --CARD-COLOR: var(--LIGHT-CARD);
  --TEXT-GREY: var(--LIGHT-TEXT-GREY);
  --HOVER-COLOR: var(--LIGHT-CARD-HOVER);
  --TOGGLE-BG-COLOR: var(--TOGGLE);
  --TOGGLE-COLOR: var(--LIGHT-BG);
}

body.dark {
  --BG-COLOR: var(--DARK-BG);
  --TEXT-COLOR: var(--DARK-TEXT-WHITE);
  --CARD-COLOR: var(--DARK-CARD);
  --TEXT-GREY: var(--DARK-TEXT-GREY);
  --HOVER-COLOR: var(--DARK-CARD-HOVER);
  --TOGGLE-BG-COLOR: linear-gradient(
    225deg,
    var(--TOGGLE-BG-START) 0%,
    var(--TOGGLE-BG-END) 90%
  );
  --TOGGLE-COLOR: var(--DARK-BG);
}

:root {
  --FONT-INTER: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 62em) {
  .container {
    max-width: 69.375rem;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.25rem 1.875rem;
  align-items: center;
}
@media (min-width: 36em) {
  .cards {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 62em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--CARD-COLOR);
  color: var(--TEXT-GREY);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 5px;
  transition: background-color 150ms ease-in-out;
}
.card:hover, .card__border:hover {
  background-color: var(--HOVER-COLOR);
}
.card--facebook {
  border-top: 5px solid var(--FACEBOOK);
}
.card--twitter {
  border-top: 5px solid var(--TWITTER);
}
.card__border {
  padding: 1.5625rem;
  background-color: var(--CARD-COLOR);
  transition: background-color 150ms ease-in-out;
}
.card--instagram {
  padding: 0.3125rem 0 0 0;
  background: linear-gradient(to right, var(--INSTAGRAM-START), var(--INSTAGRAM-END));
  border-radius: 5px;
}
.card--youtube {
  border-top: 5px solid var(--YOUTUBE);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5rem;
}
.card__platform__icon {
  margin-right: 0.5rem;
}
.card__platform__username {
  font-weight: 700;
  font-size: 0.75rem;
}
.card__count {
  font-size: 3.5rem;
  color: var(--TEXT-COLOR);
  margin-bottom: 0;
}
.card__label {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  margin-bottom: 1.5625rem;
}
.card__change {
  display: flex;
  font-size: 0.75rem;
  font-weight: 700;
  justify-content: center;
  align-items: center;
}
.card__change__icon {
  margin-right: 0.25rem;
}
.card__change__up {
  color: var(--LIMEGREEN);
}
.card__change__down {
  color: var(--BRIGHTRED);
}

.card-grid {
  background-color: var(--CARD-COLOR);
  color: var(--TEXT-GREY);
  border-radius: 5px;
  padding: 1.5625rem;
  transition: background-color 150ms ease-in-out;
  display: grid;
  gap: 1.25rem;
  grid-template-areas: "text icon" "number change";
}
.card-grid__title {
  width: 100%;
  margin: 1.375rem 0;
}
.card-grid:hover {
  background-color: var(--HOVER-COLOR);
}
.card-grid__text {
  grid-area: "text";
  font-weight: 700;
  font-size: 0.75rem;
}
.card-grid__icon {
  grid-area: "icon";
  justify-self: end;
}
.card-grid__number {
  grid-area: "number";
  color: var(--TEXT-COLOR);
  align-self: end;
}
.card-grid__change {
  grid-area: "change";
  display: flex;
  font-size: 0.75rem;
  font-weight: 700;
  align-self: end;
  align-items: center;
  justify-content: end;
}
.card-grid__change__icon {
  margin-right: 0.25rem;
}
.card-grid__change__up {
  color: var(--LIMEGREEN);
}
.card-grid__change__down {
  color: var(--BRIGHTRED);
}

.toggle {
  border: none;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 36em) {
  .toggle {
    display: grid;
    grid-template-columns: 1fr 3rem;
    gap: 0.625rem;
    width: auto;
  }
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TEXT-GREY);
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle__wrapper {
  grid-column: 2/3;
  height: 1.5rem;
  display: flex;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0%;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
}
.toggle__background {
  height: 100%;
  display: block;
  position: absolute;
  width: 100%;
  border-radius: 0.75rem;
  background: var(--TOGGLE-BG-COLOR);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  align-self: center;
  left: calc(100% - 1.3125rem);
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--TOGGLE-COLOR);
  transition: all 0.15s ease-in-out;
}
@media (prefers-color-scheme: dark) {
  .toggle__button {
    left: 0.1875rem;
  }
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
}
.toggle #dark:checked ~ .toggle__button {
  left: 0.1875rem;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
.header__title {
  width: 100%;
}
.header hr {
  border: none;
  margin: 1.25rem 0;
  background-color: var(--TEXT-GREY);
  opacity: 50%;
  height: 1px;
}
@media (min-width: 36em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
  .header__title {
    width: auto;
  }
  .header hr {
    display: none;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  color: var(--TEXT-GREY);
  font-weight: 700;
}